.wrapper {
  background-image: url("http://clipart-library.com/images/8czrEzgji.jpg");
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.card {
  background: #fff;
  border-radius: 2px;
  height: 175px;
  margin: 1rem;
  position: relative;
  width: 175px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card > .img-container {
  height: 80%;
  overflow: hidden;
  text-align: center;
  background: white;
  margin-top: 30px;
  margin-bottom: -20px;
  width: 100%;
}
.card > .img-container > img {
  width: 55%;
}
.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.navbar {
  max-height: 70px;

  width: 100%;

  color: white;
  font-size: calc(10px + 14 * (100vw - 320px) / 800);
  z-index: 50;
  white-space: nowrap;
}

h5 {
  text-align: center;
  background: red;
  padding: 15px;
}

.lead {
  text-align: center;
}

h1 {
  text-align: center;
}

.main-container {
  background: red;
  color: white;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

